<template>
	<div class="seller" ref="menuWrapper">
		<div class="cont">
			<div class="top">
				<div class="name">{{seller.name}}</div>
				<div class="stars">
					<el-rate
					  v-model="seller.score"
					  text-color="#ff9900"
					  text-template="{value}">
					</el-rate>
				</div>
				<span class="pir1">
					({{seller.ratingCount}})
				</span>
				<span class="pir2">
					{{seller.sellCount}}
				</span>
			</div>
			<div class="tab">
				<div class="tab-item">
				<br>
					<div class="price">20 <div class="yuan"></div>	</div>

				</div>
				<div class="tab-item">
				<br>
					<div class="price">4 <div class="yuan"></div></div>
				</div>
				<div class="tab-item">
				<br>
					<div class="price">39 <div class="yuan"></div></div>
				</div>
			</div>
			<div class="name"></div>
			<div class="footer">
					{{seller.bulletin}}
			</div>
			<div class="center">
				<ul>
					<li v-if="seller.supports"> <img src="../../imgs/discount.png" height="36" width="36" alt=""> </li>
					<li v-if="seller.supports"> <img src="../../imgs/discount.png" height="36" width="36" alt=""> {{seller.supports[1].description}}		</li>
					<li v-if="seller.supports"> <img src="../../imgs/decrease.png" alt="">{{seller.supports[0].description}} </li>
					<li v-if="seller.supports"> <img src="../../imgs/decrease.png" height="32" width="32" alt=""> {{seller.supports[2].description}}	</li>
					<li v-if="seller.supports"> <img src="../../imgs/invoice.png" alt=""> {{seller.supports[3].description}}	</li>
					<li v-if="seller.supports"> <img src="../../imgs/guarantee.png" alt="">{{seller.supports[4].description}} </li>
				</ul>
			</div>
			<div class="name">
				
			</div>
			<div class="middleline"></div>
			<div class="pic"  ref="shijingWrapper" >
					<ul class="imgul" ref="pics"> 
						<li class="imgli" v-for=" v in  seller.pics"><img :src="v" alt=""></li>
					</ul>
			</div>
			<div class="middleline"></div>

			<div class="name">
				
			</div>
			<div class="fot">
				<ul v-if="seller.infos">
					<li >{{seller.infos[0]}}</li>
					<li >{{seller.infos[1]}}</li>
					<li >{{seller.infos[2]}}</li>
					<li >{{seller.infos[3]}}</li>
				</ul>
			</div>
			<div class="love">
				<i class="icon iconfont icon-aixin1" id="love" @click="change()" ><div class="ii" >{{msg}}</div></i>
			</div>
		</div>
		
	</div>
</template>

<script>
	import {mapGetters} from 'vuex'
	import Star from '../star/Star.vue'
	export default {
		data(){
			return{
				on:true				
			}
		},
		computed : {
			...mapGetters([
				'seller'
			]),
			msg(){
				if(!this.on){
					return "";
				}else{
					return "";
				}
			}
				
		},
		created(){
            this.$store.dispatch('getSeller');
            this.$nextTick( () => {
				setTimeout(() => {
					this._initMenu();
					this._initShijing();
				},100)
			})
        },
        components:{
			'star':Star
		},

        methods:{
        	_initMenu(){
				this.menuBS  = new BScroll(this.$refs.menuWrapper,{
					click:true
				});
				
			},
			
			_initShijing(){
				let margin = 4;
				let width = 120;
				this.$refs.pics.style.width=( width + margin )*this.seller.pics.length-margin+'px';
               // console.log('gggg')
              this.shijingWrapper = new BScroll ( this.$refs.shijingWrapper ,{
                    click : true,
                    scrollY: 0,
                    scrollX: true,

                });
           
			},
			change(){
				this.on = !this.on;
				if(!this.on){
					document.getElementById("love").style.color="red";
				}else{
					document.getElementById("love").style.color="grey";
				}
			}

        }

	}	
</script>

<style lang='less' scoped>
	.seller{
		.love{
			width:0.8rem;
			position: absolute;
			top:0.3rem;
			right:0rem; 
			text-align: center;
			font-size:0.50rem ;
			color:tomato;
			line-height: 0.45rem;
			font-weight: bold;
			i{
				font-weight: bold;
				padding-left: 0.1rem;
			}
			.ii{
				font-size: 0.23rem;
				color:black;
			}
		}
		.middleline{
			width: 100%;
            height: 0.29rem;
            background-color: #ddfddf;
		};
		overflow: hidden;
		position: absolute;
		top: 3.48rem;
		bottom: 0.92rem;
		width: 100%;
		// display: flex;
		z-index: -5;
		.cont{
			position:absolute;
			top:0.24rem;
			left: 0.36rem;
			right: 0.36rem;
			// bottom: 0.64rem; 
			// border: 1px solid red ;
			.top{
				border-bottom:1px solid rgba(7,17,27,0.1); 
				.name{
				font-size:0.28rem;
				color:rgb(7,17,27);
				line-height:0.28rem;
				font-weight:bold;
				margin-bottom: 0.16rem;
				}
				.stars{
					margin-top: 0.26rem;
					margin-bottom: 0.26rem;
				}
				
				.pir1{
					padding-bottom: 0.36rem;
					position:absolute;
					left:2.7rem;
					top:0.82rem;
					font-size:0.24rem;
					color:rgb(77,85,93);
					line-height: 0.2rem;
				}
				.pir2{
					position:absolute;
					left:3.3rem;
					top:0.82rem;
					font-size:0.24rem;
					color:rgb(77,85,93);
					line-height: 0.2rem;
				}
			}

			
			.tab{
		        height:1.2rem;
		        line-height: 0.2rem;
		        font-size: 0.2rem;
		        width:100%;
		        display:flex;
		        color: rgb(147,153,159);
		        border-bottom: 0.01rem solid rgba(7,17,27,0.1);
		        padding-top:0.36rem;
		        .tab-item{
		            flex:1;
		            text-align: center;
		            .price{
		            	margin-top:0.16rem;
		            	margin-bottom: 0.36rem;
		            	font-size: 0.48rem;
		            	font-weight: 200;
		            	color: rgb(7,17,27);
		            	line-height: 0.48rem;
		            	display: inline-block;
						.yuan{
							display: inline-block;
							font-size: 0.2rem;
						}
		            }
		        }
		    }
		    .name{
				font-size:0.28rem;
				color:rgb(7,17,27);
				line-height:0.28rem;
				font-weight:bold;
				margin-bottom: 0.16rem;
				margin-top: 0.16rem;
			}
	        .footer{
	        	margin-top: 0.16rem;
	        	padding-left: 0.24rem;
	        	padding-right: 0.24rem;
	        	font-size: 0.24rem;
	        	font-weight: 200;
	        	color:rgb(240,20,20);
	        	line-height: 0.40rem;
	        }
	        .center{

				font-size: 0.24rem;
				font-weight: 200;
				color:black;
				line-height: 0.24rem;
				ul{
					margin-top:0.56rem;
					margin-bottom: 0.48rem;
					margin-left: 0.25rem;
					li{
						
						padding:0.20rem 0;
						img{
							vertical-align: middle;
							width: 0.32rem;
							height:0.32rem;
						}
					}
				}
			}

			.pic{
				padding-top: 0.24rem;
				padding-bottom: 0.24rem;
				width: 6.8rem;
				overflow: hidden;
				.imgul{
					display: flex;
					width: 10rem;
					li{
						margin-right: 0.08rem;
						img{
							width: 2.4rem;
							height: 1.8rem;
						}
					}
						
				}
			}
			.fot{
				font-size: 0.24rem;
				font-weight: 200;
				color:rgb(7,17,27);
				ul{
					margin-top:0.48rem;
					margin-bottom: 0.48rem;
					margin-left: 0.25rem;
					li{
						padding:0.32rem 0;
					}
				}
			}
		    
		}
	}
</style>


