<template>
	<div class="goods">
		<div class="goods-menu " ref="menuWrapper">
			<ul>
				<li v-for="(item,index) in goods" @click='selectMenu(index,$event)'  class="menu-list" :class = "{'current' : index === currentIndex}">
					<span class="text">{{item.name}}</span>
				</li>
			</ul>
		</div>
		<div class="goods-foods" ref="foodsWrapper">
			<div class="right">
				<ul>
					<li v-for="(item,indexI) in goods" class="food-list food-list-hook" >
						<p class="itemName">{{item.name}}</p>
						<ul>
							<li v-for="(food,indexJ) in item.foods" class="food"  @click = 'showfood(food,$event)'>
								<img :src="food.icon" class="foodicon">
								<div class="foodinfo">
									<p class="foodinfoname">{{food.name}}</p>
									<p class="zou">{{food.description}}</p>
									<p class="foodinfocount">{{food.sellCount}}&nbsp;{{food.rating}}%</p>
									<div class="foodinfoprice">
									    <p class="pic"><span class="yen">&yen;</span>{{food.price}}
									    <cartControl :food ='food'></cartControl></p>
									</div>
									
								</div>
								
							</li>
						</ul>
					</li>
				</ul>
			</div>
				
		</div>
		<foodDetail></foodDetail>
	</div>
</template>

<script>
	import {mapGetters} from 'vuex'
	import cartControl from '../Cartcontrol/Cartcontrol.vue'
	import foodDetail from '../detail/foodDetail.vue'
	export default {
		data(){
			return {
				listHeight : [],
				scrollY : 0
			}
		},

		created(){
            this.$store.dispatch('getGoods');
            this.$nextTick( () => {
				setTimeout(() => {
					this._initMenu();
					this._initFood();
					this._calcHeight();
				},100)
			})
        },
		computed:{
            ...mapGetters([
				'goods'
		    ]),
		    currentIndex(){
		    	for( let i = 0 ; i < this.listHeight.length ; i++ ){
		    		let height1 = this.listHeight[i];
		    		let height2 = this.listHeight[i+1];
		    		if( this.scrollY >= height1 && this.scrollY < height2 ){
		    			return i;
		    		}
		    	}
		    	return 0;
		    },

		},
		methods : {
			showfood(f,e){
				console.log(123);
				if( !e._constructed ){
					return;
				};
				this.$store.dispatch('showfood',f);
			},
			selectMenu(key,e){
				if( !e._constructed ){
					return;
				}
            	let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
				let el = foodList[key];
				this.foodBS.scrollToElement(el,300);

			},
            _calcHeight(){
            	let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
            	let height = 0;
            	this.listHeight.push(height);
            	for(let i = 0 ; i < foodList.length ; i++){
            		height += foodList[i].clientHeight;
            		this.listHeight.push(height);
            	}
            	// console.log(this.listHeight);
            },
			_initMenu(){
				this.menuBS = new BScroll(this.$refs.menuWrapper,{click:true});
			},
			_initFood(){
				
				this.foodBS = new BScroll(this.$refs.foodsWrapper,{
					probeType : 3,
					click:true
				});
				this.foodBS.on('scroll',(pos) =>{
					this.scrollY = Math.abs(Math.round(pos.y));
					// console.log(this.scrollY);
				})
			},
		},
		components:{
			foodDetail,
			cartControl
		}
        
	}
	
</script>

<style lang='less' scoped>
	.goods{
		position: absolute;
		top: 3.48rem;
		bottom: 0.92rem;
		width: 100%;
		display: flex;
		position: absolute;
		overflow: hidden;
		.goods-menu{
			flex:0 0 1.6rem;
			width: 1.6rem;
			background-color: #eaf5f7;
			.menu-list{
				&.current {
					border-right: 3px solid #ccc;
					// border-radius: 10px;
				}
				height: 1.08rem;
				background: white;
				// border-bottom: 1px solid #000;
				padding:0 0.24rem;
				display: table;
				.text{
					text-align: center;
					vertical-align: middle;
					display: table-cell;
					width: 1.12rem;
				}
			}
		}
		.goods-foods{
			flex:1;
			width: 5.9rem;
			.itemName{
				padding-left: 0.28rem;
				font-size: 0.28rem;
				line-height: 0.52rem;
				height: 0.48rem;
				color: rgb(147,153,159);
			}
			.food{
				display: flex;
				border-bottom: 0.02rem solid rgba(7,17,27,0.1);
				margin: 0.36rem;
				padding-bottom: 0.36rem;
				.foodicon{
					width: 1.5rem;
					height: 1.5rem;
				}
				.foodinfo{
					margin-left: 0.2rem;
					.icon{
						position: absolute;
						right: 0.36rem;
						color:#1296db;
						font-size: 0.48rem;
						line-height: 0.48rem;
					}
					.foodinfoname{
						font-size: 0.28rem;
						line-height: 0.28rem;
						padding-top: 0.04rem;
						color:rgb(7,17,27);
					}
					.zou{
						font-size: 0.2rem;
						// line-height: 0.3rem;
						padding-top: 0.16rem;
						color:rgb(147,153,159);
						// font-weight: normal/700;
					}
					.foodinfocount{
						width: 3.5rem;
						font-size: 0.28rem;
						line-height: 0.28rem;
						padding-top: 0.16rem;
						color:rgb(147,153,159);
						font-weight: normal/700;
						display: flex;
						justify-content:space-between;
					}
					.foodinfoprice{
							width:3.5rem;
							font-size: 0.28rem;
							line-height: 0.28rem;
							color: red;
							padding-top: 0.2rem;
							.yen{
							font-size: 0.2rem;
							
							}
						
					}
				}
			}
			
		}
	}
</style>


