<template>
	<div class="cartcontrol">
	<transition name='move'>
		<div class="reduce" v-show = 'food.count' @click.stop = 'decrement'> <i class="icon iconfont icon-jian" ></i> </div>
	</transition>  
		<div class="count" v-show = 'food.count'>{{food.count}}</div>
		<div class="add" @click.stop = 'increment'> <i class="icon iconfont icon-jia-copy"></i> </div>
	</div>
</template>

<script>
    import Vue from 'vue';
	import {mapGetters} from 'vuex'

	
	export default {
		props:['food'],
		methods:{
			increment(){
				if( !this.food.count ){
					Vue.set(this.food , 'count' ,1);
				}else{
					this.food.count ++;
				}
			},
			decrement(){
				if( this.food.count ){
					this.food.count --;
				}
			}
		}
	}
</script>

<style lang='less' scoped>
	.cartcontrol{
        //             
        .move-enter-active, .move-leave-active {
            transition: all 0.5s;
        }
        .move-leave-active {
            transition: all 0.5s;
            transform:translate(30px) rotate(90deg);
        }
        // count>0v-show
        .move-enter, .move-leave-active {
            transform:translate(20px) rotate(90deg);
            opacity: 0
        }
		width: 90px;
		height: 30px;
		position: absolute;
		right: 0.36rem;
		.add{
			position: absolute;
			right: 0;
			.icon{
				font-size: 0.32rem;
				color:rgb(0,160,220);
				font-weight: bold;
			}
		}
		.reduce{
			position: absolute;
			left: 0;
			.icon{
				font-size: 0.32rem;
				color:rgb(0,160,220);
				font-weight: bold;
			}
		}
		.count{
			position: absolute;
			left: 30px;
			color:black;
		}
		div{
			width: 25px;
			height: 30px;
			line-height: 30px;
			text-align: center;
			display: inline-block;
		}
	}
</style>


