<template>
	<transition enter-active-class="animated slideInRight" leave-active-class="animated slideOutRight">
		<div class="detail" v-show ='detailshow'>
			<div class="cont">
				<div class="top">
					<div class="name">{{seller.name}}</div>
					
					<div class="stars">
						<star :score = 'seller.score' ></star>
					</div>
				</div>
				<div class="line">
					<hr class="h1">
					
					<hr class="h2">
				</div>
				<div class="center">
					<ul>
						<li v-if="seller.supports"> <img src="../../imgs/decrease.png" alt=""> {{seller.supports[0].description}}	</li>
						<li v-if="seller.supports"> <img src="../../imgs/decrease.png" alt=""> {{seller.supports[1].description}}	</li>
						<li v-if="seller.supports"> <img src="../../imgs/discount.png" alt=""> {{seller.supports[2].description}}	</li>
						<li v-if="seller.supports"> <img src="../../imgs/discount.png" alt=""> {{seller.supports[3].description}}	</li>
						<li v-if="seller.supports"> <img src="../../imgs/special.png" alt=""> 	{{seller.supports[4].description}} </li>
					</ul>
				</div>
				<div class="line1">
					<hr class="h1">
					
					<hr class="h2">
				</div>
				<div class="footer">
					{{seller.bulletin}}
				</div>
				<div class="icon"><i style='color:#ccc;font-size:26px;' @click = 'close' value="close" class="el-icon-circle-cross"></i></div>
			</div>
		</div>
	</transition>
</template>

<script>
	import {mapGetters} from 'vuex'
	import 'animate.css/animate.css'
	import Star from '../star/Star.vue'
	export default {
		computed : {
			...mapGetters([
				'detailshow',
				'seller'
			])
		},
		methods : {
			close(){
				this.$store.dispatch('closedetail')
			}
		},
		components:{
			'star':Star
		}
	}
</script>

<style lang='less' scoped>
	.detail{
		position: fixed;
		top:0rem;
		left: 0rem;
		right: 0rem;
		bottom: 0rem;
		// border: 1px solid red
		z-index: 9999999;
		background: black;
        background: rgba(7,17,27,0.8);
        color: rgb(240,20,20);
        .cont{
        	color:white;
        	position: fixed;
			top:1.28rem;
			left: 0.72rem;
			right: 0.72rem;
			bottom: 0.64rem;
			// border:1px solid red;
			.top{
				text-align: center;
				font-size: 0.42rem;
				font-weight: 700;
				color:rgb(255,255,255);
				line-height: 0.32rem;
				margin-bottom: 0.56rem;
				.name{
					margin-bottom: 0.32rem;
				}
			}
			.line{
				
				font-size: 0.26rem;
				font-weight: 700;
				color:rgb(255,255,255);
				line-height: 0.45rem;

				.h1{
					vertical-align: middle;
					width:2.24rem;
					display: inline-block;
					line-color:rgba(255,255,255,0.2);
					margin-right: 0.1rem;
				}
				.h2{
					vertical-align: middle;
					width:2.24rem;
					display: inline-block;
					line-color:rgba(255,255,255,0.2);
					margin-left: 0.1rem;
				}	
			}
			.center{

				font-size: 0.24rem;
				font-weight: 200;
				color:rgb(255,255,255);
				line-height: 0.24rem;
				ul{
					margin-top:0.56rem;
					margin-bottom: 0.48rem;
					margin-left: 0.25rem;
					li{
						
						padding:0.23rem 0;
						img{
							vertical-align: middle;
							width: 0.32rem;
							height:0.32rem;
						}
					}
				}
			}
			.line1{
				
				font-size: 0.26rem;
				font-weight: 700;
				color:rgb(255,255,255);
				line-height: 0.45rem;

				.h1{
					vertical-align: middle;
					width:2.24rem;
					display: inline-block;
					line-color:rgba(255,255,255,0.2);
					margin-right: 0.1rem;
				}
				.h2{
					vertical-align: middle;
					width:2.24rem;
					display: inline-block;
					line-color:rgba(255,255,255,0.2);
					margin-left: 0.1rem;
				}	
			}
			.footer{
				margin-top: 0.48rem;
				margin-left:0.1rem;
				font-size: 0.24rem;
				font-weight: 200;
				color:rgb(255,255,255);
				line-height: 0.48rem;
			}
			.icon{
				position: absolute;
				bottom:0.05rem;
				left: 45%;
				display: block;
				z-index: 99;
			}
        }
	}
</style>


