<template>
	<transition enter-active-class="animated slideInRight" leave-active-class="animated slideOutRight">			
		<div class="goodsdetail" v-show="foodshow" ref='menuWrapper'>
			<div style="height:600px">
				<div class="detail-content" >
					<div class="image-header">
						<img :src="food.image" alt="">
					</div>
					<div class="cont">
						<div class="foodinfo">
							<p class="foodinfoname">{{food.name}}</p>
							<p class="zou">{{food.description}}</p>
							<p class="foodinfocount">{{food.sellCount}}&nbsp;{{food.rating}}%</p>
							<div class="foodinfoprice">
							    <p class="pic"><span class="yen">&yen;</span>{{food.price}}</p>
							</div>
						</div>
						<div class="container">
							<div class="show">
								<cartcontrol :food = 'food'></cartcontrol>
							</div>
							<div class="car" @click = 'add' v-show ='!food.count'>
								
							</div>
						</div>
						<div class="middleline" v-if="food.info"></div>
						<div class="jieshao" v-if="food.info">
							<p class="title"></p>
							<span class="tex">
								{{food.info}}
							</span>
						</div>
						<div class="middleline"></div>
						<div class="pinjia">
							<p class="title"></p>
							<div class="pingjia">
								<ul v-if="food.ratings">
									<li class="all"  @click="getAll" ><span class="text">{{food.ratings.length}}</span></li>
									<li class="jian"  @click="ok" ><span class="text">{{countNum}}</span></li>
									<li class="cao"   @click="no" ><span class="text">{{food.ratings.length-countNum}}</span></li>
								</ul>
								<div  class="loo" ><i class="icon iconfont icon-103" :class="{'checked':flag}" @click="changeFlag"></i></div>
							</div>			
						</div>
						<div class="content">
						    <div class="menu-wrapper">
						      <ul>
						        <li v-for="(v,k) in ratLists" :key='k' class="menu-list" v-show="all || yes === v.rateType">
						          <div class="rat">
						            <img :src="v.avatar" alt="" class='pic'>
						            <div class="ratR">
						              	<span>{{v.username}}</span>
									 	<span class="data">{{v.rateTime | date}}</span>
						              	<!-- <div class="star">
						      			<el-rate
										  v-model="v.score"
										  text-template="{value}" class="ele">
										</el-rate><span class="time">{{v.deliveryTime}}</span>
						              	</div> -->
						              	<p class="text">{{v.text}}</p>
						              	<div class="satisfy">
						                  <span v-if="v.rateType == 0" class="good"><i class="icon iconfont icon-haoping"></i></span>
						                  <span v-else="v.rateType == 1" class="bad"><i class="icon iconfont icon-chaping"></i></span>
						                  <div v-for="(v,k) in v.recommend" class="foods" v-if="k<3">
						                      <span>{{v}}</span>
						                  </div>
						              </div>
						            </div>
						          </div>
						        </li>
						      </ul>
						    </div>
					  </div>

					</div>
					<div class="hide" @click = 'hidefood'>
						<i class="el-icon-d-arrow-left"></i>
					</div>
				</div>
			</div>
		</div>
	</transition>
</template>

<script>
	import {mapGetters} from 'vuex'
	import 'animate.css/animate.css'
	import cartcontrol from '../Cartcontrol/Cartcontrol.vue'
	import Vue from 'vue'
	import BScroll from 'better-scroll'
	export default {
		data() {
            return {
                all: true,
                yes: -1,
                flag: false
            }
        },
        created() {
            this.$store.dispatch('getRatings');
            this.$nextTick(() => {
                setTimeout(() => {
                    this._initMenu();
                }, 100)
            })
		},
		computed:{
			...mapGetters([
				'foodshow',
				'food',
				'seller',
                'ratings'
			]),
			ratLists() {
                if (this.flag) {
                    return this.food.ratings.filter((item) => {
                        return item.text
                    })
                }
                return this.food.ratings;
            },
            countNum() {
                return this.food.ratings.filter((item) => {
                    return item.rateType === 0
                }).length;
            }
		},
		methods:{
			hidefood(){
				this.$store.dispatch('hidefood');
			},
			_initMenu(){
					this.menuBS = new BScroll(this.$refs.menuWrapper,{scrollX: 0,click:true});
			},
			add(){
				Vue.set(this.food , 'count' ,1);
			},
			changeFlag() {
                this.flag = !this.flag
            },
            refresh() {
                setTimeout(() => {
                    this.menuBS.refresh();
                }, 20)
            },
            ok() {
                this.yes = 0
                this.all = false
            },
            no() {
                this.yes = 1
                this.all = false
            },
            getAll() {
                this.yes = -1
                this.all = true
            },
		},
		components:{
			cartcontrol,
		},
		filters:{
            date:function(value){
                function toDouble (n){
                    return n<10?'0'+n:n;
                }
                var d = new Date(value);
                return d.getFullYear()+"-"+toDouble(d.getMonth()+1)+"-"+toDouble(d.getDate())+" "+toDouble(d.getHours())+":"+toDouble(d.getMinutes());
            }
        },
			
	}
</script>

<style lang="less" scoped>
	.goodsdetail{
		background-color: white;
		position:fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		.detail-content{
			.image-header{
				position: relative;
				width: 100%;
				height: 0;
				padding-bottom: 100%;
				img{
					position: absolute;
					width: 100%;
					height: 100%;
					left: 0;
					top:0;
				}
			}
			.cont{
				position: fixed;
				top:7.9rem;
				left:0.36rem;
				right: 0.36rem;
				bottom:0.55rem;
				.middleline{
					width: 100%;
		            height: 0.29rem;
		            background-color: #f3f5f7;
				};
				.container{
					width: 90px;
					height: 30px;
					position: absolute;
					top:1.2rem;
					.car{
						position: absolute;
						z-index: 9;
						background-color:rgb(0,160,220) ;
						text-align: center;
						line-height: 0.6rem;
						color: white;
						left:4.89rem;
						border-radius:15px; 
						width: 100%;
						height: 100%;
					}
					.show{
						position: absolute;
						left:7rem;
					}
				}
				.title{
					position: relative;
					font-size: 0.28rem;
					line-height: 0.28rem;
					padding-top: 0.36rem;
					color:rgb(7,17,27);
					font-weight: 700;
					margin-bottom: 0.24rem;
				}
				.pingjia{
					position: relative;
					top:0.1rem;

					ul{
						li{
							width:1.4rem;
							height: 0.6rem;
							margin-right:0.3rem;
							display: inline-block;
							text-align: center;
							line-height: 0.6rem;  
						}
						.all{
							background-color: rgb(0,160,220);
						}
						.jian{
							background-color: rgba(0,160,220,0.3);
						}
						.cao{
							background-color: rgba(77,85,93,0.3);
						}
					}
					.loo{
						padding-top: 0.3rem;
						font-size:0.24rem;
						color: rgb(147,153,159);
						line-height: 0.46rem;
						i{
							font-size: 0.48rem;
							line-height: 0.48rem;
							padding-right: 0.08rem;
						}
						.checked{
							color:tomato;
						}
					}
				}
				.content {
					position: relative;
					top:0.2rem;
					.menu-wrapper {
						width: 7.2rem;
						height: 4rem;
						.menu-list {
							.rat {
								margin: 0.36rem 0.36rem 0 0.36rem;
								display: flex;
								border-bottom:1px solid rgba(7, 17, 27, 0.1);
								.pic {
									border-radius: 50%;
									width: 0.56rem;
									height: 0.56rem;
								}
								.ratR {
									margin-left: 0.24rem;
									.data{
										position: absolute;
			    						right: 0.36rem;
										color: #93999f;
									}
									.star {
										display: flex;
										margin: 0.08rem 0 0.12rem 0;
										.time {
											font-size: 0.2rem;
											font-weight: 200;
											color: rgb(147, 153, 159);
											line-height: 0.42rem;
			    							height: 0.48rem;
			    							margin-left:0.18rem; 
										}
									}
									.text {
										font-size: 0.24rem;
										line-height: 0.36rem;
										color: rgb(7, 17, 27);
									}
									.satisfy {
										display: flex;
										.good {
											color: rgb(0, 160, 220);
											font-size: 0.24rem;
											line-height: 0.84rem;
										}
										.bad {
											color: rgb(147, 153, 159);
											font-size: 0.24rem;
											line-height: 0.84rem;
										}
										.foods {
											display: flex;
											span {
												overflow: hidden;
												white-space: nowrap;
												text-overflow: ellipsis;
												margin: 0.16rem;
												border: 0.02rem solid rgba(7, 17, 27, 0.1);
												border-radius: 0.02rem;
												background-color: white;
												font-size: 0.18rem;
												color: rgb(147, 153, 159);
												padding:0.12rem ;
												width: 1rem;
												margin-bottom:0.36rem;
											}
										}
									}
								}
							}
						}
					}
				}
				.tex{
					font-size: 0.24rem;
					font-weight: 200;
					color:rgb(77,85,93);
					line-height: 0.48rem;
					padding-bottom:0.36rem;

				}
				.foodinfoname{
						font-size: 0.28rem;
						line-height: 0.28rem;
						padding-top: 0.04rem;
						color:rgb(7,17,27);
						font-weight: 700;
					}
					.zou{
						font-size: 0.2rem;
						padding-top: 0.16rem;
						color:rgb(147,153,159);
					}
					.foodinfocount{
						font-size: 0.20rem;
						line-height: 0.20rem;
						padding-top: 0.16rem;
						color:rgb(147,153,159);
					}
					.foodinfoprice{
						width:3.5rem;
						font-size: 0.32rem;
						line-height: 0.28rem;
						color: red;
						padding-top: 0.2rem;
						padding-bottom: 0.2rem;
						.yen{
							font-size: 0.2rem;
						}
					}

			}
			.hide{
				position: fixed;
				top:10px;
				left: 10px;
				width: 12px;
				height: 10px;
				padding:10px;
				color:black;
				font-weight:1000;
				z-index:2;
				background:#ccc;
				border-radius: 0.50rem;
			}
		}
	}
</style>


