<template>
	<div class="rating" >
		<div class="cont">
			<div class="rating-left" >
				<div class="pf">
					{{seller.score}}
				</div>
				<div class="zh"></div>
				<div class="baif">{{seller.rankRate}}%</div>
			</div>
			<div class="rating-right" >
				<ul>
					<li>
						<el-rate
						  v-model="seller.serviceScore"
						  disabled
						  text-template="{value}" class="ele">
						</el-rate>
						<span class="ser">{{seller.serviceScore}}</span>
					</li>
					<li>
						<el-rate
						  v-model="seller.foodScore"
						  disabled
						  text-template="{value}" class="elem">
						</el-rate>
						<span class="food">{{seller.foodScore}}</span>
					</li>
					<li>
						<span></span>
						<span class="fz">{{seller.deliveryTime}}</span>
					</li>
				</ul>
					
			</div>
		</div>

		<div class="pingjia">
			<ul>
				<li class="all"  @click="getAll" ><span class="text">{{ratings.length}}</span></li>
				<li class="jian"  @click="ok" ><span class="text">{{countNum}}</span></li>
				<li class="cao"   @click="no" ><span class="text">{{ratings.length-countNum}}</span></li>
			</ul>
			<div  class="loo" ><i class="icon iconfont icon-103" :class="{'checked':flag}" @click="changeFlag"></i></div>				
		</div>
	  	<div class="content">
		    <div class="menu-wrapper" ref="menuWrapper">
		      <ul>
		        <li v-for="(v,k) in ratLists" :key='k' class="menu-list" v-show="all || yes === v.rateType">
		          <div class="rat">
		            <img :src="v.avatar" alt="" class='pic'>
		            <div class="ratR">
		              	<span>{{v.username}}</span>
					 	<span class="data">{{v.rateTime | date}}</span>
		              	<div class="star">
		      			<el-rate
						  v-model="v.score"
						  text-template="{value}" class="ele">
						</el-rate><span class="time">{{v.deliveryTime}}</span>
		                 <!--  <Star :score='v.score' :size="'small'"></Star> -->
		              	</div>
		              	<p class="text">{{v.text}}</p>
		              	<div class="satisfy">
		                  <span v-if="v.rateType == 0" class="good"><i class="icon iconfont icon-haoping"></i></span>
		                  <span v-else="v.rateType == 1" class="bad"><i class="icon iconfont icon-chaping"></i></span>
		                  <div v-for="(v,k) in v.recommend" class="foods" v-if="k<3">
		                      <span>{{v}}</span>
		                  </div>
		              </div>
		            </div>
		          </div>
		        </li>
		      </ul>
		    </div>
	  </div>
	</div>
</template>

<script>
    import { mapGetters } from 'vuex'
	import BScroll from 'better-scroll'
	
    export default {
        data() {
            return {
                all: true,
                yes: -1,
                flag: false
            }
        },
       
        computed: {
            ...mapGetters([
                'seller',
                'ratings'
            ]),
            ratLists() {
                if (this.flag) {
                    return this.ratings.filter((item) => {
                        return item.text
                    })
                }
                return this.ratings;
            },
            countNum() {
                return this.ratings.filter((item) => {
                    return item.rateType === 0
                }).length;
            }
		},
		filters:{
            date:function(value){
                function toDouble (n){
                    return n<10?'0'+n:n;
                }
                var d = new Date(value);
                return d.getFullYear()+"-"+toDouble(d.getMonth()+1)+"-"+toDouble(d.getDate())+" "+toDouble(d.getHours())+":"+toDouble(d.getMinutes());
            }
        },
        created() {
            this.$store.dispatch('getRatings');
            this.$nextTick(() => {
                setTimeout(() => {
                    this._initMenu();
                }, 100)
            })
		},
		
        methods: {
            changeFlag() {
                this.flag = !this.flag
                this.refresh()
            },
            refresh() {
                setTimeout(() => {
                    this.menuBS.refresh();
                }, 20)
            },
            _initMenu() {
                this.menuBS = new BScroll(this.$refs.menuWrapper);
            },
            ok() {
                this.yes = 0
                this.all = false
                this.refresh();
            },
            no() {
                this.yes = 1
                this.all = false
                this.refresh()
            },
            getAll() {
                this.yes = -1
                this.all = true
                this.refresh()
            },
        },
    }

</script>

<style lang='less' scoped>
	.rating{
		position: absolute;
		top: 3.48rem;
		bottom: 0.92rem;
		width: 100%;
		z-index: -5;
		.cont{
			display: flex;
			position:absolute;
			top:0.24rem;
			left: 0.36rem;
			right: 0.36rem;
			bottom: 0.64rem; 
			// border: 1px solid red ;
			.rating-left{
				flex:0.75; 
				height:2.5rem;
				// border: 1px solid red;
				text-align: center;
				.pf{
					margin-top: 0.36rem;
					font-size: 0.40rem;
					color:rgb(255,153,0);
					line-height: 0.56rem;
				}
				.zh{
					margin-top: 0.26rem;
					font-weight: bold;
					font-size: 0.24rem;
					color:rgb(7,17,27);
					line-height: 0.24rem;
				}
				.baif{
					margin-top: 0.26rem;
					font-size: 0.2rem;
					color:rgb(147,153,159);
					line-height: 0.2rem;
				}
			}
			.rating-right{
				height:2.5rem;
				flex:1.25; 
				// border: 1px solid red;
				margin-left: 0.30rem;
				ul{
					font-size: 0.24rem;
					color:rgb(7,17,27);
					line-height: 0.36rem;
					font-weight: bold;
					margin-top: 0.36rem;
					li{
						padding-bottom: 0.26rem;
						.fz{
							color:rgb(147,153,159);
							padding-left: 0.24rem;
						}
						.ele{
							position:absolute; 
							top:0.35rem;
							left:3.8rem;
							padding-left: 0.20rem;
						}
						.ser{
							position: relative;
							left:2.3rem;
							font-size: 0.24rem;
							color:rgb(255,153,0);
							line-height: 0.36rem;
						}
						.elem{
							position:absolute; 
							top:0.95rem;
							left:3.8rem;
							padding-left: 0.20rem;
						}
						.food{
							position: relative;
							left:2.3rem;
							font-size: 0.24rem;
							color:rgb(255,153,0);
							line-height: 0.36rem;
						}
					}
				}
				
			}
		}
		.pingjia{
			position: absolute;
			top:3.18rem;
			left:0.48rem;
			ul{
				li{
					width:1.4rem;
					height: 0.6rem;
					margin-right:0.3rem;
					display: inline-block;
					text-align: center;
					line-height: 0.6rem;
				}
				
				.all{
					background-color: rgb(0,160,220);
				}
				.jian{
					background-color: rgba(0,160,220,0.3);
				}
				.cao{
					background-color: rgba(77,85,93,0.3);
				}
			}
			.loo{
				padding-top: 0.3rem;
				font-size:0.24rem;
				color: rgb(147,153,159);
				line-height: 0.46rem;
				i{
					font-size: 0.48rem;
					line-height: 0.48rem;
					padding-right: 0.08rem;
					
				}
				.checked{
					color:tomato;
				}
			}
		}
		.content {
				position: absolute;
				top:4.8rem;
				left:0.16rem;
			.menu-wrapper {
				width: 7.2rem;
				overflow: hidden;
				height: 4rem;
				.menu-list {
					.rat {
						margin: 0.36rem 0.36rem 0 0.36rem;
						display: flex;
						border-bottom:1px solid rgba(7, 17, 27, 0.1);
						.pic {
							border-radius: 50%;
							width: 0.56rem;
							height: 0.56rem;
						}
						.ratR {
							margin-left: 0.24rem;
							.data{
								position: absolute;
	    						right: 0.36rem;
								color: #93999f;
							}
							.star {
								display: flex;
								margin: 0.08rem 0 0.12rem 0;
								.time {
									font-size: 0.2rem;
									font-weight: 200;
									color: rgb(147, 153, 159);
									line-height: 0.42rem;
	    							height: 0.48rem;
	    							margin-left:0.18rem; 
								}
							}
							.text {
								font-size: 0.24rem;
								line-height: 0.36rem;
								color: rgb(7, 17, 27);
							}
							.satisfy {
								display: flex;
								.good {
									color: rgb(0, 160, 220);
									font-size: 0.24rem;
									line-height: 0.84rem;
								}
								.bad {
									color: rgb(147, 153, 159);
									font-size: 0.24rem;
									line-height: 0.84rem;
								}
								.foods {
									display: flex;
									span {
										overflow: hidden;
										white-space: nowrap;
										text-overflow: ellipsis;
										margin: 0.16rem;
										border: 0.02rem solid rgba(7, 17, 27, 0.1);
										border-radius: 0.02rem;
										background-color: white;
										font-size: 0.18rem;
										color: rgb(147, 153, 159);
										padding:0.12rem ;
										width: 1rem;
										margin-bottom:0.36rem;
									}
								}
							}
						}
					}
				}
			}
		}
	
	}
</style>


