
import types from '../types.js'
import axios from 'axios'

const state = {
	goods:[],
  foodshow : false,
  food:{},
}

const getters = {
 	goods(state){
    return state.goods;
  },
  food(state){
    return state.food;
  },
  foodshow(state){
    return state.foodshow;
  },
 
}

const actions = {
  getGoods({commit,state}){
    axios.get('../../../static/data.json').then( resp => {
      if( resp.data.errno === 0 ){
        commit(types.GET_GOODS,resp.data.goods)
      }
    })
  },
  showfood({commit},food){
    commit(types.SHOW_FOOD,food);
  },
  hidefood({commit}){
    commit(types.HIDE_FOOD);
  },
  getRatings:({commit,state}) => {
    axios.get('/api/ratings').then((resp) => {
          if(resp.data.errno===0){
            commit(types.GET_RATINGS,resp.data.data);
          }
      });
  }
 
}


const mutations = {
  [types.GET_GOODS](state,data){
    state.goods = data;
  },
  [types.HIDE_FOOD](state){
    state.foodshow = false;
  },
  [types.SHOW_FOOD](state,food){
    state.foodshow = true;
    state.food = food;
  },
  [types.GET_RATINGS](state,data){
    state.ratings=data;
  }
}
export default {
  state,
  getters,
  actions,
  mutations
}



