<template>
    <div id="app">
        <v-header></v-header>
        <div class="tab">
            <div class="tab-item">
                <router-link to='/goods'></router-link>
            </div>
            <div class="tab-item">
                <router-link to='/ratings'></router-link>
            </div>
            <div class="tab-item">
                <router-link to='/seller'></router-link>
            </div>
        </div>
        <router-view></router-view>
        <v-footer></v-footer>
    </div>
</template>

<script>
    import Header from './components/header/header.vue'
    import Footer from './components/footer/footer.vue'
    export default {
        components : {
            'v-header' : Header,
            'v-footer':Footer
        }
    }
</script>

<style lang='less' scoped>
    .tab{
        position: absolute;
        top:2.68rem;
        height:0.8rem;
        line-height: 0.8rem;
        font-size: 0.28rem;
        width:100%;
        display:flex;
        z-index:-999999999;
        color: rgb(240,20,20);
        border-bottom: 1px solid rgba(7,17,27,0.1);
        .tab-item{
            flex:1;
            text-align: center;
            & > a {
