<template>
    <div class="header">
    	<div class="header-top">
    		<div class="background">
    			<img :src="seller.avatar">
    		</div>
    		<img :src="seller.avatar" alt="" class="avatar" @click='show'>
    		<div class="content">
    			<p class="title"><img src="../../imgs/brand.png"  alt="" class="icon1"> {{seller.name}}</p>
        		<p class="time">{{seller.description}} / {{seller.deliveryTime}}</p>
      			<p class="youhui" v-if="seller.supports"><img src="../../imgs/decrease.png" alt="" class="icon2">{{seller.supports[0].description}}</p>
    		</div>
    	</div>
        <div class="bottom">
        	<div class="footer">
        		<div class="bull">
        		<span><img src="../../imgs/bulletin.png"  class="icon3" style="padding-bottom:0.1rem;"></span>
        		<span><marquee width=400 behavior=alternate direction=left align=middle style="padding-top:0.06rem;color:#ccc;"> {{seller.bulletin}}</marquee> </span>
        		</div>
        		<i class="icon iconfont icon-jiankuohaocudayou"></i>
        	</div>
        </div>
        
        <div class="radio" @click='show'>
        	5&nbsp;>
        </div>	
        <Detail></Detail>	
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    import Detail from '../detail/Detail.vue'
    export default {
        computed:{
            ...mapGetters([
                'seller',
            ]),
        },
        components:{
        	Detail
        },
        created(){
            this.$store.dispatch('getSeller');
        },
        methods:{
        	show(){
        		this.$store.dispatch('showdetail')
        	}
        }
    }    
</script>
<style lang="less" scoped>
    .header{
		height: 2.68rem;
		background: gray;
        background: rgba(7,17,27,0.5);
		width: 100%;
		position: absolute;
		overflow: hidden;
		.background{
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			z-index: -5;
			filter:blur(5px);
			img{
				width: 100%;
				height: 100%;
			}
		} 
		.avatar{
			width:1.28rem;
			height: 1.28rem;
			border-radius: 0.04rem;
			padding-top: 0.48rem;
			padding-left: 0.48rem;
			display: inline-block;
		}
		.content{
			display: inline-block;
			margin-left:0.32rem;
			padding:0.04rem; 
			.title{
				font-size: 0.32rem;
				color: rgb(255,255,255);
				font-weight: bold;
				line-height: 0.36rem;
				padding-bottom: 0.16rem;
				.icon1{
					vertical-align: middle;
					width:0.58rem;
					height:0.38rem;
					margin-right:0.08rem;
				}
			}
			.time{
				font-size: 0.24rem;
				color:rgb(255,255,255);
				font-weight: 200;
				line-height: 0.24rem;
				padding-bottom:0.2rem;
			}
			.youhui{
				font-size: 0.24rem;
				color: rgb(255,255,255);
				font-weight: 200;
				line-height: 0.24rem;
				.icon2{
					vertical-align: middle;
					width:0.35rem;
					height:0.3rem;
					margin-right:0.08rem;
				}
			}
		}
		
		.bottom{
			background-color:rgba(7,17,27,0.2) ;
			height: 0.56rem;
			width: 100%;
			position: absolute;
			bottom: 0;
			.footer{
				padding:0.1rem  0.24rem  0.1rem  0.24rem; 

				.bull{
					width: 6.5rem;
					font-size: 0.15rem;
					color:rgb(255,255,255);
					font-weight: 200;
					overflow: hidden;
					display: inline-block;
					white-space: nowrap;
					text-overflow:ellipsis;
					.icon3{
						padding-right: 0.08rem;
						vertical-align: middle;						
						width: 0.55rem;
						height: 0.32rem;

					}
				}
				.icon{
					color:white;
					font-size: 0.25rem;
					position:absolute;
					right:0.3rem;
					top:0.15rem;
				}

			}
		}
		.radio{
			position: absolute;
			font-size: 0.21rem;
			background:rgba(0,0,0,0.2);
			top:1.35rem;
			right:0.24rem;
			border-radius: 0.3rem;
			width: 1rem;
			text-align: center;
			height: 0.48rem;
			line-height: 0.48rem;
			color: #ccc;
			font-weight: 500;
		}
	}
</style>


