<template>
    <div class="shopcart">
        <div class="content">
            <div class="content-left">
                <div class="logo-wrap" @click="showMore">
                    <div class="logo" style="font-size:0.48rem;" >
                       <i class="icon iconfont icon-ic_shopping_cart_p" :class='{"nu":totalPrice > 0}'></i>
                    </div>
                    <div class="num" v-show="totalPrice >0 ">{{totalCount}}</div>
                </div>
                <div class="price" :class='{"en":totalPrice > 0}'>{{totalPrice}}</div>
                <div class="desc">4</div>
            </div>
            <div class="content-righ" @click.stop.prevent = 'pay'>
                <div class="pay":class='{"enough":totalPrice >= seller.minPrice}'>
                    {{payDesc}}
                </div>
            </div>
        </div>
        <transition enter-active-class="animated slideInUp" leave-active-class="animated slideOutDown">
        <div class="showpage" v-show="listshow">
            <div class="tit">
                <div class="tit-left"></div>
                <div class="tit-right" @click="empty"></div>
            </div>
        
            <div  style="max-height:4.8rem;overflow:hidden;" ref="menuWrapper">
                <ul >
                    <li v-for="v in selectFoods" >
                        <span class="name">{{v.name}}</span>
                        <span class="price"> {{v.price * v.count}}</span>
                        <span class="icon"><cartcoltrol :food = 'v'></cartcoltrol></span>
                    </li>
                </ul>
            </div>
      
        </div>
        </transition>
        <div class="allpage" v-show="listshow" @click="showpage"></div>
    </div>
    
</template>

<script>   
    import {mapGetters} from 'vuex'
    import cartcoltrol from '../Cartcontrol/Cartcontrol.vue'
    export default {
        data(){
            return{
                show:false
            }
        },
        methods : {
            showMore(){
                if(!this.totalCount){
                    return;
                }
                this.show = !this.show;
            },
            showpage(){
                this.show = false;
            },
            empty(){
                this.selectFoods.forEach(food => {
                   food.count=0;
                   this.show = false;
                });
            }
        },
        components : {
            cartcoltrol
        },
        computed:{
            ...mapGetters([
                'seller',
                'goods'
            ]),
            listshow(){
                if(this.totalCount==0){
                    this.show=true;
                }
                if(!this.totalCount){
                    return;
                }
                let show =!this.show;
                if(show){
                     this.$nextTick( () => {
                       if(!this.cc){
                            this.cc = new BScroll(this.$refs.menuWrapper,{
                                click:true
                            });
                       }else{
                            this.cc.refresh();
                       }
                    })
                }
                return show;
            },
            selectFoods(){
                let foods = [];
                this.goods.forEach( good => {
                    good.foods.forEach( food => {
                        if (food.count > 0){
                            foods.push(food);
                        }
                    }) 
                })
                return foods;
            },
            totalPrice(){
                let total = 0;
                this.selectFoods.forEach(food => {
                    total += food.price * food.count;
                });
                return total;
            },
            totalCount(){
                let count = 0;
                this.selectFoods.forEach(food => {
                    count += food.count;
                });
                return count;
            },
            payDesc(){
                if(this.totalPrice === 0 ){
                    return `${this.seller.minPrice}`
                }else if( this.totalPrice < this.seller.minPrice){
                    return `${this.seller.minPrice - this.totalPrice}`
                }else{
                    return ""
                }
            }
        }

    };

</script>

<style lang="less" type="text/less">
        
    .shopcart{
        .showpage{
            overflow: hidden;
            width: 100%;
            max-height: 4.4rem;
            background: white;
            bottom:1rem;
            z-index:2;
            position: absolute;
            .tit{
                padding-bottom:0.16rem; 
                background-color: #ccc;
                display: flex;
                justify-content:space-between;
                .tit-left{
                    margin-left:0.36rem;
                    margin-top:0.16rem;
                    font-size: 0.36rem;
                    font-weight: 500;
                    color:rgb(7,17,27);
                }
                .tit-right{
                    margin-right:0.36rem;
                    margin-top:0.16rem;
                    font-size: 0.32rem;
                    font-weight: 500;
                    color:rgb(0,160,220);
                    display: block; 
                }
            }
            ul{
                 margin-left:0.36rem;
                 margin-right:0.36rem;
                 padding-bottom:0.36rem;
                li{
                    font-size: 0.28rem;
                    color:rgb(7,17,27);
                    line-height: 0.48rem;
                    width:100%;
                    height: 0.96rem;
                    line-height: 0.8rem;
                    .icon{
                        position: absolute;
                        margin-top:0.06rem;
                        right:0.1rem;
                    }  
                    .price{
                        position: absolute;
                        margin-top:0rem;
                        left:3.6rem;
                        color: red;
                    } 
                }
            }
        }
        .allpage{
            position: absolute;
            top:0;
            left:0;
            right:0;
            bottom:0;
            z-index:0;
            background-color:rgba(7,17,27,0.6);
            fillter:blur(5px);
        }
        .content{
            position: absolute;
            height:1rem;
            width: 100%;
            bottom: 0;
            left: 0;
            z-index:99;
            display: flex;
            background: #141d27;
            .content-left{
                flex:1;   
                .logo-wrap{
                    display: inline-block;
                    vertical-align: top;
                    position: relative;
                    top: -0.2rem;
                    margin-left: 0.24rem;
                    margin-bottom: 0.04rem;
                    padding:0.12rem;
                    background: #141d27;
                    border-radius: 50%;
                    box-sizing: border-box;
                    .logo{
                        width: 0.88rem;
                        height: 0.88rem;
                        line-height: 0.88rem;
                        text-align: center;
                        // background: white;
                        background-color: #2b343c;
                        &.highlight{
                            background:#00a0dc;
                        }
                        border-radius: 50%;
                        i{
                            font-size: 0.48rem;
                            color:rgba(255,255,255,0.4);
                            &.highlight{
                                color: white;
                            }
                        }
                        .nu{
                    		color:#1296db;
                    	} 
                    }
                     
                    .num{
                        position: absolute;
                        top:0;
                        right: 0;
                        width: 0.48rem;
                        height: 0.32rem;
                        line-height: 0.37rem;
                        text-align: center;
                        border-radius: 0.16rem;
                        font-size: 0.09rem;
                        font-weight: 700;
                        color:white;
                        background: rgb(240,20,20);
                        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
                    }
                  
                }  

                .price{
                    display: inline-block;
                    font-size:0.32rem;
                    font-weight: 700;
                    line-height: 0.48rem;
                    color:#ccc;
                    vertical-align: top;
                    margin-top: 0.24rem;
                    box-sizing: border-box;
                    padding-right: 0.24rem;
                    border-right: 1px solid rgba(255,255,255,0.1);
                    &.highlight{
                        color: #fff;
                    }
                }   
                .en{
                	font-size:0.32rem;
                	color:white;
                	font-weight: 700;
                    line-height: 0.48rem;
                }
                .desc{
                    display: inline-block;
                    font-size: 0.30rem;
                    font-weight: 300;
                    line-height: 0.48rem;
                    color:rgba(255,255,255,0.4);
                    vertical-align: top;
                    margin-top: 0.28rem;
                    box-sizing: border-box;
                    padding-left: 0.24rem;
                }           
            }
            .content-righ{
                flex:0 0 2.05rem;
                width: 2.05rem;
                .pay{
                    font-size: 0.24rem;
                    font-weight: 700;
                    line-height: 1.0rem;
                    height: 1.0rem;
                    color:#26333b;
                    background-color: #2b343c;
                    color:rgba(255,255,255,0.4);
                    text-align: center;
                    &.not-enough{
                        background: #2b333b;
                    }
                    &.enough{
                        background: #00b43c;
                        color: #fff;
                    }
                }
            }
        }
    }
    
</style>



